<template lang="">
    <div v-if="isShow" class="form-edit px-5 py-4">
            <div  class="exit-icon d-inline-block">
                <svg @click="change" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x-lg" viewBox="0 0 16 16">
                <path d="M1.293 1.293a1 1 0 0 1 1.414 0L8 6.586l5.293-5.293a1 1 0 1 1 1.414 1.414L9.414 8l5.293 5.293a1 1 0 0 1-1.414 1.414L8 9.414l-5.293 5.293a1 1 0 0 1-1.414-1.414L6.586 8 1.293 2.707a1 1 0 0 1 0-1.414z"/>
                </svg>
            </div>
            <p class="py-1 m-0 text-center fw-bold">
                {{ token.list[indexToken].code }}
            </p>
            <p class="text-center">Ngày bắt đầu: {{ formatTime(token.list[indexToken].created_at) }} </p>
            <div  class="py-2 ">
                <input  class="form-control px-3"  data-date-format="DD MM YYYY" v-model="dataToken.date"  placeholder="id account" type="date" >
                <select v-model="dataToken.month"  class="form-select my-2">
                    <option selected :value=".5" >15 ngày</option>
                    <option :value="1" >1 tháng</option>
                    <option :value="3" >3 tháng</option>
                    <option :value="4" >4 tháng</option>
                </select>
                <button @click="updateToken(token.list[indexToken].id)"  type="button" class="btn btn-reuse px-5 d-block mx-auto">Update</button>
            </div>

            <div  class="py-2 d-flex justify-content-between">
                  <input  class="form-control " v-model.number="idAddAcount" placeholder="ID Account" type="number" >
                  <button @click="addAccforToken(token.list[indexToken].id)" type="button" class="mx-1 btn btn-reuse px-4">Add</button>
            </div>

            <!--  -->
            <div v-for="(account, index) in  token.list[indexToken].accounts" :key="index"
                class="py-2" >
                <input :id="'info-account-' +  account.id " class="form-check-input " :value="account.id" type="checkbox" v-model="listAccReuse">
              
                <label class="mx-2"  :for="'info-account-' + account.id ">  {{ account.username }} - {{ account.password }}</label>
            </div>
            <!--  -->

            <div class="d-flex  justify-content-between my-2">
                <button @click="removeAccount(token.list[indexToken].id)"  type="button" class="btn btn-delete px-5">Delete</button>
                <button @click="reuseAccount(token.list[indexToken].id)" type="button" class="btn btn-reuse px-5">Reuse</button>
            </div>
        </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      listAccReuse: [],
      idAddAcount: null,
      dataToken: {
        month: 0.5,
        date: null,
      },
    };
  },
  props: {
    isShow: Boolean,
    indexToken: Number,
  },
  computed: { ...mapState(["token"]) },

  methods: {
    reuseAccount(id) {
      this.$store.dispatch("reuseAccount", [id, this.listAccReuse]);
      this.listAccReuse = [];
    },
    removeAccount(id) {
      this.$store.dispatch("removeAccount", [id, this.listAccReuse]);
      this.listAccReuse = [];
    },
    change() {
      this.$emit("popDown");
    },
    addAccforToken(id) {
      this.$store.dispatch("addAccountForToken", [id, this.idAddAcount]);
    },
    updateToken(id) {
      if (this.dataToken.date != null && this.dataToken.date != "")
        this.$store.dispatch("updateToken", [id, this.dataToken]);
      else alert("Date đâu");

      this.dataToken.date = null;
    },
    formatTime(time) {
      let date = parseInt(time.slice(8, 11));
      let month = parseInt(time.slice(5, 7));
      let year = time.slice(2, 4);
      var hour = parseInt(time.slice(11, 13)) + 7; // UTC + 7
      let minute = time.slice(14, 16);

      if (hour >= 24) {
        var over_hour = hour % 24;
        date += 1;
        hour = over_hour;
      }
      let arrMonth = [1, 3, 5, 7, 8, 10, 12];
      arrMonth.filter((e) => {
        if (month == e && date == 32) {
          month += 1;
          date = 1;
        }
      });
      let timing =
        date + "/" + month + "/" + year + " " + parseInt(hour) + ":" + minute;

      return timing;
    },
  },
};
</script>
<style lang="css" scoped>
.form-edit {
  position: fixed;
  z-index: 99;
  top: 50%;
  left: 50%;
  width: fit-content;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgba(158, 158, 158, 0.603);

  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(20px);
}
.form-edit .btn-reuse {
  background: #22bb33;
  color: #fff;
  border-radius: 7px;
}
.form-edit .btn-delete {
  background: #f44336;
  color: #fff;
  border-radius: 7px;
}
.form-edit .exit-icon {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  width: 30px;
  height: 30px;
}
.form-edit .exit-icon:hover svg {
  color: #f44336;
}
</style>