<template lang="">
    <div class="manager">
        <ManagerEdit @popDown="isShow =! isShow" :isShow="isShow" :indexToken="indexToken"/>
        <div class="manager-navbar mx-auto d-flex justify-content-between w-50">
            <div>
                <input class="form-check-input" type="radio" value="all" id="checkbox" v-model="isBan">
                <label class="form-check-label" style="color: #fff" for="checkbox">All</label>
            </div>
            <div>
                <input class="form-check-input" type="radio" value="banned" id="checkbox1" v-model="isBan">
                <label class="form-check-label" style="color: #fff" for="checkbox1">Banned</label>
            </div>
            <div>
                <input class="form-check-input" type="radio" value="expired" id="checkbox2" v-model="isBan">
                <label class="form-check-label" style="color: #fff" for="checkbox2">Expired</label>
            </div>
            <Search/>
        </div>
        <div class="manager-main">
            <ListToken @popUp="popUp" />
             <Paginate/>
        </div>
    </div>
</template>
<script>
import { mapActions } from "vuex";
import { mapState } from "vuex";
import Search from "../Search.vue";
import Paginate from "./components/Paginate.vue";
import ManagerEdit from "./components/ManagerEdit.vue";
import ListToken from "./components/ManagerListTokens.vue";

export default {
  components: {
    Search,
    Paginate,
    ManagerEdit,
    ListToken,
  },
  data() {
    return {
      isShow: false,
      indexToken: 0,
      isBan: "all",
    };
  },
  watch: {
    isBan() {
      if (this.isBan == "banned") {
        this.$store.dispatch("getFilterTokens");
      }
      if (this.isBan == "expired") {
        this.$store.dispatch("getFilterBannedTokens");
      }
      if (this.isBan == "all") {
        this.$store.dispatch("getTokens");
      } //
    },
  },
  mounted() {
    this.checkLogin();
    this.getTokens();
  },
  computed: { ...mapState(["token"]) },
  methods: {
    ...mapActions(["checkLogin", "getTokens"]),
    edit(index) {
      this.index = index;
      this.isShow = true;
    },
    popUp(data) {
      this.isShow = !this.isShow;
      this.indexToken = data;
    },
  },
};
</script>
<style lang="css" scoped>
.manager {
  width: 98%;
  margin: 0 auto;
}

.manager .form-control {
  border-radius: 20px;
  width: 100%;
  padding: 8px 20px;
}
.manager span {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 4px 10px;
  border: 1px solid #000;
  border-radius: 5px;
}

.manager-status .status_valid {
  background: #fff;
}
.status_invalid {
  background: #f1e7cd;
}
.status_ban {
  background: rgb(219, 13, 13);
}
@media only screen and (max-width: 600px) {
  .manager {
    width: 1240px;
    position: relative;
    left: calc(-50vw + 50%);
  }
}
</style>