<template lang="">
    <div class="home container d-flex align-items-center pb-5">
        <div class="introduce ">
            <h3 class="description animate__animated animate__fadeInDown">hệ thống tự động 24/7</h3>
            <h1 class="home-title animate__animated animate__fadeInUp">Tận hưởng thế giới phim không lo về giá</h1>
            <button id="btnGetAccount" style="padding: 0" type="button" class="btn  btn-getAccount animate__animated animate__bounceInLeft">
                <router-link class="btn-home nav-link " to="/get-account">Nhận tài khoản
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-search" viewBox="0 0 16 16">
                    <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
                    </svg>
                </router-link>
            </button>
        </div>
        <!-- <div class="image-home">
            <img src="../../assets/storefront_illustration.png" >
        </div> -->
        <div class="wrap-sliders">
            <div class="list-slider "
               
            >
                <div class="slider py-5 px-3 d-flex align-items-stretch"
                    v-for="(slide, index) in list_slide"
                    :key="index"
                    :class="[animationName,list_slide[active].title , active == index ? 'slider_active' : 'slider_none']"
                    v-touch:swipe.left="swipeSlideLeft"  
                    
                    >
                    <div class="slider-decr">
                        <h3>
                            {{ list_slide[active].title}} Premium
                        </h3>
                        <p>
                            {{ list_slide[active].decr}}
                        </p>
                      
                        <button type="button" class="btn mx-auto d-block btn-link m-2">
                            <router-link id="nav-link" class="nav-link " to="/get-account">
                                Xem giá ngay
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-search" viewBox="0 0 16 16">
                                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
                                </svg>
                                
                            </router-link>
                        </button>
                    </div>
                    <div class="slider-img">
                        <img src="../../assets/Netflix.png" >
                    </div>
                    
                </div>
                
            </div>
                <div class="d-flex justify-content-center p-2">
                    <ul class="d-inline-block list-dots">
                        <li v-for="(slide,index) in  list_slide" 
                            :key="index"
                            class="dot"
                            :class="active === index ? 'dot_active': ''"
                            @click="jump(index)"                     
                        >   
                        </li>
                    </ul>
                </div>
        </div>
    </div>
</template>
<script>
export default {
    mounted() {
        this.timer = setInterval(()=>{
            this.active += 1;
            if(this.active == this.list_slide.length)
                this.active = 0;
        }, 6000)
    },
    data() {
        return {    
            active: 0,   
            timer: 0,
            animationName: 'pre',  
            list_slide: [
                {
                    title: 'youtube',
                    decr: 'Không lo nhức đầu mỏi gối, Trial 4 tháng chỉ 79K'
                },
                {
                    title: 'netflix',
                    decr: 'Xem phim thỏa thích. Mang cả rạp phim về nhà chỉ từ 30K tháng'
                },
                 {
                    title: 'disney',
                    decr: 'Thế giới Marvel và những bộ phim bom tấn sớm về Việt Nam'
                }
            ]
        }
    },
    methods: {
        resetTimer() {
          clearInterval(this.timer);
            this.timer = setInterval(()=>{
                this.active += 1;
                if(this.active == this.list_slide.length)
                    this.active = 0;
            }, 6000)
        },
        jump(index) {
            this.active <= index ? this.animationName = 'pre' : this.animationName = 'next'
            this.active = index;
            this.resetTimer();
        },
        swipeSlideLeft() {
        
          this.animationName = 'pre'
          if(this.active < this.list_slide.length)
            this.active += 1;
          if(this.active == this.list_slide.length)
            {
            this.active = 0;
            this.animationName = 'next'
  
            }
        
           this.resetTimer();
        }
        
    },
};
</script>
<style lang="css" scoped>
.list-dots {
    list-style: none;
    margin: 0;
    padding: 0;
    line-height: 20px;
    overflow:auto;
}
.dot {
    width: 24px;
    height: 8px;
    background: rgba(255, 255, 255, 0.705);
    margin: 3px;
    padding: 4px;
    float: left;
    display: block;
    transition: all .3s ease;
    border-radius: 10px;
    cursor: pointer;
}
.dot_active {
    background: #fff;
    width: 50px;
}
.slider_active {
    display: block;
}
.slider_none {
    display: none !important;
}
.home {
  height: 86vh;
}
.introduce {
  width: 45%;
  padding-right: 15px;
}
.image-home, .wrap-sliders {
  width: 55%;
}
.home .description {
  text-transform: uppercase;
  color: #ffc947;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 8px;
}

.home-title {
  font-weight: bold;
  font-size: 55px;
  color: #fff;
}

.home .btn-home {
  background-color: #ffc947;
  border-radius: 30px;
  color: #0a1931;
  border: 2px solid #0a1931;
  box-shadow: 2px 2px #0a1931;
  transition: ease all 0.2s;
  padding: 12px 18px;
}
.home .btn-home:hover {
  border: 2px solid #0a1931;
  box-shadow: 1px 1px #0a1931;
  transform: translate(2px, 2px);
}
.home .btn-getAccount a {
  color: #0a1931;
  font-weight: bold;
}
.home .btn-getAccount svg {
  margin: 0 6px;
}
.image-home img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.list-slider{
    overflow-x: hidden;
    transition: transform 0.5s ease-in-out;
}
.slider {
    border-radius: 15px;
    display: none;
}
.youtube {
    background: linear-gradient(76.83deg, #F73B3B 0.76%, #F5AF19 100%);   
}
.netflix {
    background: linear-gradient(90deg, rgba(237,25,9,1) 0%, rgba(235,21,221,1) 100%);
}
.disney {
  background: rgb(76,195,34);
  background: linear-gradient(302deg, rgba(76,195,34,1) 0%, rgba(45,225,253,1) 100%);

}
.fade {
    animation: fade .1s;
    animation-timing-function: linear;
    min-width: 100%;
}
@keyframes fade {
  from {  
        opacity: .1;
  }
  to {
        opacity: .9;
  }
}
.pre {
    animation: fillIn .5s;
    animation-timing-function: linear;
    min-width: 100%;
}
@keyframes fillIn {
  from {
      transform: translateX(300px);
      opacity: .1;
  }
  to {
        transform: translateX(0px);
        opacity: .9;
  }
}
.next {
    animation: fillOut .5s;
    animation-timing-function: linear;
    min-width: 100%;
}
@keyframes fillOut {
  from {
      transform: translateX(-300px);
      opacity: .1;
  }
  to {
        transform: translateX(0px);
        opacity: .9;
  }
}
.slider-decr h3 {
    text-transform: uppercase;
    text-align: center;
    font-size: 26px;
    font-weight: bold;
    color: #0a1931;
}
.slider-decr p {
    text-align: center;
    color: #fff;
    text-transform:initial;
    padding: 12px;
    margin: 0;

}
.slider-decr, .slider-img {
    width: 50%;
}
.slider-img img {
    width: 100%;
}
.btn-link {
    color: #fff;
    font-weight: bold;

}
.btn-link #nav-link {
    text-decoration: none !important;
    background: #fff;
    color: #333;
    border: none;
    box-shadow: none;
    border-radius: 20px;

}
.btn-link .nav-link:hover {
    color: #fff !important;
    
}
@media only screen and (max-width: 1024px) {
  .home {
    height: 100vh;
    flex-direction: column-reverse;
    justify-content: center;
  }
  .home > div {
    width: 100%;
  }
  .home .description {
    font-size: 16px;
  }
  .home-title {
    font-size: 32px;
    line-height: 40px;
  }
}
@media only screen and (max-width: 600px) {
  .home {
    flex-direction: column-reverse;
    justify-content: end;
    padding: 0 !important;
    height: 100%;
  }
  .slider-img {
    display: none;
  }
  .slider-decr {
    width: 100%;
  }
  .image-home {
    width: 80%;
    margin: 20px;
  }
  .introduce {
    width: 100%;
    padding: 30px 30px;
  }
  .home .description {
    color: #ffc947;
    font-size: 16px;
  }
  .home-title {
    font-size: 36px;
    line-height: 50px;
  }
  #btnGetAccount {
    padding: 0 !important;
    display: block;
    width: 100%;
  }
  .home .btn-getAccount a {
    padding: 10px;
  }
}
@media only screen and (max-width: 400px) {

  .introduce {
    padding: 25px 5px 0 5px;
  }
  .home {
    height: unset;
    justify-content:space-between !important;
    width: 98%;
  }
  .home .description {
    letter-spacing: 2px;
  }
  .home-title {
    font-size: 26px;
  }
  .slider {
    padding: 25px 20px 10px 20px !important;
    margin-top: 25px;
  }
}
@media only screen and (max-width: 300px) {
  .home {
    height: 80vh;
    justify-content: space-around;
  }
  .home .description {
    letter-spacing: 0px;
    font-size: 16px;
  }
  .home-title {
    font-size: 28px;
    line-height: 30px;
  }
}
</style>